import React from "react";

const ModalDele = (props) => {
    return(
        <div className='home_modal_par'>
          <div className='home_modal'>
            <div className='home_modal_title'>Xo sn phm {props.spXoa} ?</div>
            <div className='home_modal_comment home_modal_table' >{props.BanSelected} , {props.phanCap} </div>
            <div className='home_modal_comment' >Sau khi xo ton b sn phm s mt vnh vin khi bn c chn. Bn c ng  xa sn phm ?</div>
            <div className='home_modal_btn_par'>
              <button onClick={() => props.setModalDelete(false)} className='home_modal_btn'>Hy</button>
              <button onClick={()=>props.XoaSPCT()} className='home_modal_btn'>ng </button>
            </div>
          </div>
        </div>
    );
}

export default ModalDele;